import { ArrowUpDown } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import { Column } from "./types";

interface TableHeaderProps {
  columns: Column[];
  onColumnEdit?: (column: Column) => void;
  onColumnDelete?: (columnId: string) => void;
  onAddColumn?: () => void;
  onMoveColumn?: (sourceId: string, targetId: string) => void;
  sortConfig?: { columnId: string; direction: 'asc' | 'desc' } | null;
  onSort?: (config: { columnId: string; direction: 'asc' | 'desc' } | null) => void;
  selectedRows?: string[];
  totalRows?: number;
  onSelectAll?: () => void;
}

export function TableHeader({
  columns = [],
  onColumnEdit,
  onColumnDelete,
  onAddColumn,
  onMoveColumn,
  sortConfig,
  onSort,
  selectedRows = [],
  totalRows = 0,
  onSelectAll
}: TableHeaderProps) {
  // Add safety check to prevent runtime errors
  if (!columns || !Array.isArray(columns)) {
    return (
      <thead>
        <tr className="border-b bg-muted/50">
          <th className="h-10 px-2 text-left align-middle text-xs font-medium text-muted-foreground">
            No columns defined
          </th>
        </tr>
      </thead>
    );
  }

  return (
    <thead>
      <tr className="border-b bg-muted/50">
        {/* Select all checkbox */}
        <th className="w-[40px] px-2">
          <Checkbox
            checked={totalRows > 0 && selectedRows.length === totalRows}
            onCheckedChange={onSelectAll}
            aria-label="Select all rows"
          />
        </th>
        {columns.map((column) => (
          <th
            key={column.id}
            className="h-10 px-2 text-left align-middle text-xs font-medium text-muted-foreground"
          >
            <div className="flex items-center gap-2">
              <span>{column.title}</span>
              {column.type !== 'checkbox' && (
                <Button
                  variant="ghost"
                  size="sm"
                  className="-ml-3 h-8 hover:bg-transparent"
                  onClick={() => {
                    if (onSort) {
                      const isCurrentColumn = sortConfig?.columnId === column.id;
                      const nextDirection = !isCurrentColumn || sortConfig?.direction === 'desc' ? 'asc' : 'desc';
                      onSort({ columnId: column.id, direction: nextDirection });
                    }
                  }}
                >
                  <ArrowUpDown className="h-4 w-4" />
                </Button>
              )}
            </div>
          </th>
        ))}
        <th className="w-[40px]" /> {/* Actions column */}
      </tr>
    </thead>
  );
}