"use client";

import { useState } from 'react';

interface ContextMenu {
  visible: boolean;
  x: number;
  y: number;
  type: 'row' | 'cell';
  data?: {
    rowId?: string;
    columnId?: string;
  };
}

export function useContextMenu() {
  const [contextMenu, setContextMenu] = useState<ContextMenu>({
    visible: false,
    x: 0,
    y: 0,
    type: 'row',
  });

  const showContextMenu = (
    event: React.MouseEvent,
    type: 'row' | 'cell',
    data?: { rowId?: string; columnId?: string }
  ) => {
    event.preventDefault();
    setContextMenu({
      visible: true,
      x: event.clientX,
      y: event.clientY,
      type,
      data,
    });
  };

  const hideContextMenu = () => {
    setContextMenu((prev) => ({ ...prev, visible: false }));
  };

  return {
    contextMenu,
    showContextMenu,
    hideContextMenu,
  };
}