import { Checkbox } from "@/components/ui/checkbox";
import { Column } from "./types";

interface TableRowProps {
  columns: Column[];
  row: any;
  onUpdateRow?: (rowId: string, changes: any) => void;
  onContextMenu?: (e: React.MouseEvent, rowData: any) => void;
  onCellContextMenu?: (e: React.MouseEvent, column: Column) => void;
  isSelected?: boolean;
  onSelect?: () => void;
  selectedCells?: string[];
  onCellSelect?: (cellId: string) => void;
}

export function TableRow({ 
  columns = [], 
  row, 
  onUpdateRow,
  onContextMenu,
  onCellContextMenu,
  isSelected = false,
  onSelect,
  selectedCells = [],
  onCellSelect
}: TableRowProps) {
  return (
    <tr 
      className={`border-b hover:bg-muted/50 ${isSelected ? 'bg-blue-50' : ''}`}
      onContextMenu={(e) => onContextMenu?.(e, row)}
    >
      {/* Checkbox column */}
      <td className="w-[40px] px-2">
        <Checkbox
          checked={isSelected}
          onCheckedChange={() => onSelect?.()}
          aria-label="Select row"
        />
      </td>
      {columns.map((column) => {
        const cellId = `${row.id}-${column.id}`;
        const isCellSelected = selectedCells.includes(cellId);
        
        return (
          <td
            key={column.id}
            className={`h-10 px-2 text-left align-middle text-sm ${isCellSelected ? 'bg-blue-100' : ''}`}
            onClick={() => onCellSelect?.(cellId)}
            onContextMenu={(e) => onCellContextMenu?.(e, column)}
          >
            {/* Basic cell display - you can customize this based on column type */}
            {row?.[column.id] || ''}
          </td>
        );
      })}
      <td className="w-[40px]">
        {/* Actions cell - you can add action buttons here */}
        <div className="flex items-center justify-center">
          {/* Add action buttons like edit, delete, etc. */}
        </div>
      </td>
    </tr>
  );
}